.footer-nav-links {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer-nav-links .footerlink-title {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}
.footer-nav-links .footerlink-ul {
  list-style-type: none;
  list-style-position: inside;
  padding-left: 0px;
  height: 80px;
  max-height: 80px;
}
.footer-nav-links .footerlink-ul .footerlink-links {
  font-family: "Titillium Web";
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 14px;
  color: black;
  margin: 9px 0px;
}
.footer-nav-links .footerlink-ul .footerlink-links a {
  text-decoration: none;
}
.footer-nav-links .footerlink-ul .footerlink-links a:link {
  color: #353535;
}
.footer-nav-links .footerlink-ul .footerlink-links a:visited {
  color: #353535;
}
.footer-nav-links .footerlink-ul .footerlink-links a:hover {
  color: #a9c5db;
}
.footer-nav-links .footerlink-ul .footerlink-links a:active {
  color: #a9c5db;
}

